* {
  scroll-snap-type: y mandatory;
}
.snapElement {
  scroll-snap-align: start;
}
.heroContainer {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 1s;
}

.videoBg {
  -webkit-user-select: none;
          user-select: none;
  pointer-events: none;
  z-index: -1;
}

.content {
  display: block;
  text-align: justify;
  padding: 1rem 2rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  position: absolute;
  margin: 0.5rem;
}

.heroContainer:last-child {
  align-items: center;
}
video {
  object-fit: cover;
  height: 100vh;
  width: 100%;
  position: absolute;
}

.mainHeading {
  font-size: 150px;
}

.mainHeading ~ p {
  font-size: 40px;
}

.reveal-text {
  color: #fff;
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  -webkit-animation: revealAnimation 3s forwards;
          animation: revealAnimation 3s forwards;
}

@media only screen and (max-width: 768px) {
  * {
    scroll-snap-type: none;
  }
  .mainHeading {
    font-size: 4rem;
  }
  .mainHeading > p {
    font-size: 2rem;
  }
}

.about {
  padding-top: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  background: radial-gradient(at left top, #2373ce, #8e9eed, #2373ce);
  background-size: 400% 400%;
  -webkit-animation: gradient 5s ease infinite;
          animation: gradient 5s ease infinite;
}

.card > img {
  margin: 30px;
}

.title > header {
  color: #fff;
  font-size: 2.5rem;
}

header > span {
  color: #0d6efd6e;
}

.cards {
  -webkit-animation: revealAnimation 1s forwards;
          animation: revealAnimation 1s forwards;
}

/* WIE */
.WIE {
  display: flex;
  font-family: monospace;
  flex-direction: column;
  align-items: center;
}
.WIE_MAIN {
  min-height: 100vh;
  background-size: 400% 400% !important;
  padding: 5rem 0 0.1rem 0;
  background: linear-gradient(-45deg, #99057b, #292975, #760599, #057b99);
  -webkit-animation: gradient 5s ease infinite;
          animation: gradient 5s ease infinite;
}
.wie_photo > img {
  width: 100%;
}
.wie_title {
  color: #fff;
  font-size: xx-large;
  text-align: center;
  font-weight: 500;
  font-family: "Borel", cursive;
}

.wie_desc,
.wie_media,
.about_footer {
  text-align: justify;
  padding: 1rem 2rem;
  color: #fff;
}

.about_footer {
  font-family: monospace;
  background: rgba(0, 0, 0, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
}

@media only screen and (max-width: 1024px) {
  .about {
    padding-top: 10rem;
  }
  .card {
    width: auto;
  }
}
/* FOR DESKTOPS */
@media only screen and (min-width: 1024px) {
  .title > header {
    font-size: 3rem;
  }
  .WIE {
    flex-direction: row;
  }
  .wie_content {
    display: flex;
    flex-direction: column;
  }

  .wie_photo > img,
  .wie_title {
    width: 100%;
  }

  .wie_photo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.mentorAbout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(at left top, #2373ce, #8e9eed, #2373ce);
  background-size: 400% 400%;
  -webkit-animation: gradient 5s ease infinite;
          animation: gradient 5s ease infinite;
  padding-top: 6rem;
  min-height: 100vh;
}

.mentorAbout > header {
  text-align: center;
  font-size: 2.5rem;
  color: #fff;
}

.mentorContainer {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.mentorCard {
  padding: 18px 18px 9px;
  background: rgba(227, 242, 253, 0.35);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  width: 75%;
}

.mentorMeta {
  text-align: center;
  padding: 5px 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.25);
}
/* FOR Tablets */
@media only screen and (max-width: 1024px) and (min-width: 768px) {
  .mentorContainer {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .mentorCard {
    width: 40%;
  }
  .mentorAbout > header {
    font-size: 2.8rem;
  }
}
/* FOR Desktop */
@media only screen and (min-width: 1024px) {
  .mentorContainer {
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .mentorCard {
    width: 25%;
  }
  .mentorAbout > header {
    font-size: 3rem;
  }
}

